/* Button.css */
.custom-button {
    background-color: #01d8b1;
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    font-family: 'DM-Sans', sans-serif;
}

.custom-button:hover:enabled {
    background-color: #0aaa8d;
}

.custom-button:focus {
    outline: none;
}

.custom-button-disabled {
    cursor: pointer;
    opacity: 0.5;
    transition: none;
}