body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
  font-family: 'Roboto'; /* Nombre de la fuente */
  src: url('../public/fonts/Roboto-Regular.ttf') format('truetype'); /* Ruta a la fuente .woff2 */
  font-weight: normal; /* Peso de la fuente */
  font-style: normal; /* Estilo de la fuente */
}

@font-face {
  font-family: 'DM-Sans'; /* Nombre de la fuente */
  src: url('../public/fonts/DMSans-Medium.ttf') format('truetype'); /* Ruta a la fuente .woff2 */
  font-weight: normal; /* Peso de la fuente */
  font-style: normal; /* Estilo de la fuente */
}

/* Usa la fuente en el CSS */
body {
  font-family: 'DM-Sans', sans-serif; /* Aplicar la fuente */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
