

#home {
    background-color: #BCD9D4;
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-image: url("/public/image1.png"); /* Reemplaza con la ruta de tu imagen */
    background-size: 60%; /* Ajusta la imagen para cubrir todo el contenedor */
    background-position: center; /* Centra la imagen en el contenedor */
    background-repeat: no-repeat; /* Evita que la imagen se repita */
    background-blend-mode: overlay;
}

#header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: small;
}

#header img {
    height: 75px;
}

#first-vertical {
    border-left: none;
    padding-left: 0;
    padding-right: 3px;
}

#vertical {
    border-left: 1px solid black;
    padding-left: 3px;
    padding-right: 3px;
    display: inline;
  }


#menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-align: center;
    gap: 10px;
}

#footer {
    text-align: center;
    width: 100%;
    position:fixed;
    bottom: 0;
    height: 125px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 10px;
    font-size: 12px;
    background-color: rgba(164, 196, 180, 0.2);
}

#footer a {
    color: rgb(43, 43, 43);
    text-decoration: none;
}

#footer a:hover {
    text-decoration: underline;
}

#footer img {
    height: 40%;
}

#nav {
    display: flex;
    justify-content: center;
    height: 100px;
    width: 100%;
    align-items: start;
    flex-direction: row;
    gap: 70%;
    position: fixed;
    top: 0; /* Fija en la parte superior */
    margin: 0; /* Elimina margen inferior para evitar desplazamiento no deseado */
    font-size: 14px;
    z-index: 1000; /* Asegura que el navbar esté sobre el contenido */
    align-items: center;
}

#nav a {
    vertical-align: middle;
    color: black;
    text-decoration: none;
}

a#exit-anchor {
    color: red;
}


#nav a:hover {
    text-decoration: underline;
}

#nav img {
    height: 20px;
    margin-right: 2px;
    vertical-align: middle;
}

@media (max-width: 576px) {

    #home {
        display: fixed;
    }

    #footer {
        font-size: 8px;
        height: 90px;
    }

    .custom-menu-button {
        width: 130px; /* Ancho fijo del botón */
        height: 90px; /* Alto fijo del botón */
        font-size: 12px;

    }

    #nav {
        gap: 80px;
    }
    
    }
