/* Button.css */
.custom-text {
    background-color: #D9D9D9;
    border: 2px solid #BFBFBF;
    height: 25px;
    width: 224px;
    font-weight: normal;
    font-family: 'DM-Sans', sans-serif;
    transition: 0.5s;
}

.input-error:focus {
    transition: 0.5s;
}

.custom-text:focus {
    outline: none;
    border: 2px solid rgb(102, 102, 102);
    border-radius: 5px;
}

.custom-text:focus::placeholder {
    opacity: 0.2;
}

.input-error {
    border: 2px solid red;  /* Cambia el borde a rojo */
  }