#create-request {
    background-color: #BCD9D4;
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-image: url("/public/image1.png");
    background-size: 60%;
    background-position: center;
    background-repeat: no-repeat;
    background-blend-mode: overlay;
}

#header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: small;
    padding-bottom: 20px;
}


#header img {
    height: 50px;
}

#header p {
    font-size: larger;
}

#title {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: small;
    padding-bottom: 10px;
}

Button>img {
    height: 50px;
}


#title img {
    height: 50px;
}

#title p {
    font-size: larger;
}

#menu {
    display: grid;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    gap: 20px;
    grid-template-columns: repeat(2, 1fr);
}

#request-menu {
    text-align: center;
    gap: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#file-upload {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-size: 12px;
}

.image-upload {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}
.image-upload > img {
    height: 250px;
}

#digitos > input {
    height: 50px;
    width: 50px;
    text-align: center;
    font-family: 'Roboto';
    font-size: larger;
    background-color: #D9D9D9;
    border: 2px solid #BFBFBF;
    font-weight: normal;
    font-family: 'DM-Sans', sans-serif;
    transition: 0.5s;
}

#digitos > input:focus {
    outline: none;
    border: 2px solid rgb(102, 102, 102);
    border-radius: 5px;
}

#digitos > div {
    align-self: center;
}

#digitos {
    display: flex;
    gap: 10px;
}

.modal-content {
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 500px;
    height: 400px;
    text-align: center;
    border-radius: 10px;
    padding: 40px;
}

.modal-content > p {
    font-size: larger;
    font-style: italic;
}

#modal {
    display: flex;
    align-items: center;
    justify-content: center;
}

#confirm-modal {
    display: flex;
    align-items: center;
    justify-content: center;
}

#modal:focus {
    outline: none;
}

#btn-option {
    display: flex;
    gap: 10px;
}

#dragndrop {
    height: 150px;
    width: 150px;
    border: 5px solid black;
    border-radius: 20px;
    border-style: dashed;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    transition: 0.3s;
    background-color: white;
    font-size: smaller;
}

#dragndrop:hover {
    border-color: gray;
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.4);
}

/* CreateRequest.css */
.dropdown-large {
    width: 100%; /* Ajusta según tus necesidades, por ejemplo, 300px */
    height: 50px; /* Ajusta el alto */
    font-size: 16px; /* Aumenta el tamaño del texto */
    padding: 10px; /* Añade espacio interno */
    border-radius: 5px; /* Bordes redondeados */
    border: 1px solid 2px solid #BFBFBF; /* Borde personalizado */
    background-color: #D9D9D9;;
  }
  

.image-upload>input {
    display: none;
  }


/* (1366x768) WXGA Display */
@media  screen and (max-width: 1366px) {
    #dragndrop {
        height: 100px;
        width: 100px;
    }

    #digitos > input {
        height: 40px;
        width: 40px;
    }

    #request-menu {
        gap: 8px;
    }
}
 
