/* Button.css */
.custom-exit-button {
    background-color: #E0E0E0;
    color: rgb(246, 69, 69);
    opacity: 0.9;
    border: 2px solid red; 
    padding: 5px; /* Ajusta el padding para controlar el espaciado interno */
    font-size: 13px;
    font-weight: bold;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    text-align: center;
    display: flex;
    align-items: center; /* Alinea el contenido verticalmente en el centro */
    gap: 10px; /* Espacio entre la imagen y el texto */
    width: 200px; /* Ancho fijo del botón */
    height: 60px; /* Alto fijo del botón */
    overflow: hidden; /* Opcional: oculta cualquier contenido que desborde el botón */
    font-family: 'DM-Sans', sans-serif;

}

.custom-exit-button img {
    height: 45px; /* Ajusta el tamaño de la imagen si es necesario */
    margin-right: 3px; /* Espacio a la derecha de la imagen */
}

.custom-exit-button span {
    flex: 1; /* Permite que el texto ocupe el espacio restante */
    text-align: center; /* Centra el texto horizontalmente */
}

.custom-exit-button:hover {
    background-color: rgb(246, 69, 69);
    color: white;
}
