/* Button.css */
.custom-button-warning {
    background-color: rgb(246, 69, 69);
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    font-family: 'DM-Sans', sans-serif;
}

.custom-button-warning:hover:enabled {
    background-color: rgb(251, 0, 0);
}

.custom-button-warning:focus {
    outline: none;
}

button:disabled {
    cursor: default;
    opacity: 0.5;
    transition: none;
}