#sign-up-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100vh;
    background-color: #BCD9D4;
    background-image: url("/public/image1.png"); /* Reemplaza con la ruta de tu imagen */
    background-size: 60%; /* Ajusta la imagen para cubrir todo el contenedor */
    background-position: center; /* Centra la imagen en el contenedor */
    background-repeat: no-repeat; /* Evita que la imagen se repita */
    background-blend-mode: overlay;
}

#sign-up-form form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 8px;
}

#img {
    z-index: 0;
}

#sign-in-block {
    font-size: 14px;
    display: flex;
    align-items: center;
    margin: 0;
}

.hyperlink {
    color: black;
    text-decoration: underline;
    color: #34d5ba;
}

hr {
    margin-top: 8px;
    width: 175px;
    outline: none;
}
  
#qr {
    height: 50%;
}

#confirm-2fa {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 12px;
}

#termsandconditions {
    font-size: smaller;
    text-wrap: wrap;
    width: 400px;
    margin-top: 10px;
    margin-bottom: 10px;
}
