#patient-list {
    background-color: #BCD9D4;
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-image: url("/public/image1.png"); /* Reemplaza con la ruta de tu imagen */
    background-size: 60%; /* Ajusta la imagen para cubrir todo el contenedor */
    background-position: center; /* Centra la imagen en el contenedor */
    background-repeat: no-repeat; /* Evita que la imagen se repita */
    background-blend-mode: overlay;
}

.custom-button {
    margin-right: 10px;
}

#header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: small;
}

#header img {
    height: 75px;
}

#title {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: small;
    padding-bottom: 20px;
}


#title img {
    height: 50px;
}

#title p {
    font-size: larger;
}

#menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    gap: 1px;
}

#image {
    height: 20px;
}


/* Restablecer estilos predeterminados */
table, th, td {
    margin: 0;
    padding: 0;

    border-collapse: collapse; /* Mantener las celdas colapsadas para compatibilidad */
    width: 1%; /* Ajusta según sea necesario */
    height: 5%; /* Ajusta según sea necesario */
}

/* Estilos de la tabla principal */
#patient-list table {
    width: 100%;
    max-width: 80%;
    border-collapse: collapse; /* Mantén collapse para consistencia */
    border-radius: 15px; /* Bordes redondeados para la tabla */
    overflow: hidden; /* Evita que el contenido se desborde */
    table-layout: auto; /* Forzar el ancho fijo para evitar diferencias en el diseño */
}

/* Estilos de celdas */
#patient-list th, #patient-list td {
    padding: 10px;  /* Espacio dentro de las celdas */
    text-align: center;
}

/* Estilos para el encabezado */
#patient-list th {
    background-color: #e4e3e3;
    height: 60px;

}

/* Estilos para las celdas */
#patient-list td {
    height: 60px;
    width: 200px;
}

#patient-list tr {
    background-color: white;
    transition: 0.2s;
}

#patient-list tr:hover {
    background-color: rgb(226, 226, 226);
}

/* Ajustes adicionales para mejorar la consistencia entre navegadores */
#icon-header {
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Estilos del icono */
.icon-img {
    width: 40px;  /* Tamaño del icono */
    height: 40px;
}

#patients-btn>button {
    margin-right: 10px;
}

@media (max-width: 576px) {
    table,td,th,tr {
        font-size: 10px;
    }
    td{
        height: 20px;
    }
    .custom-button {
        padding: 5px 10px; 
        font-size: 12px; 
        margin-right: 10px;
    }

    .pagination {
        font-size: 10px;
    }

    #footer {
        height: 60px;
    }
}

/* (1366x768) WXGA Display */
@media  screen and (max-width: 1366px) {
    #footer {
        height: 100px;
    }
    .custom-button {
        padding: 5px 10px; 
        font-size: 12px;
        margin-right: 10px;
    }

    .pagination {
        font-size: 10px;
        margin-bottom: 50px;
    }
}
