.pagination {
    margin-top: 20px;
    display: flex;
    align-items: center;
    gap: 10px;
}

@media (max-width: 576px) {
    table,td,th,tr {
        font-size: 10px;
    }
    td{
        height: 20px;
    }
    .custom-button {
        padding: 5px 10px; 
        font-size: 12px; 
    }

    .pagination {
        font-size: 10px;
    }

    #footer {
        height: 60px;
    }
}

/* (1366x768) WXGA Display */
@media  screen and (max-width: 1366px) {
    #footer {
        height: 100px;
    }
    .custom-button {
        padding: 5px 10px; 
        font-size: 12px; 
    }
    .custom-button-warning {
        padding: 5px 10px; 
        font-size: 12px; 
    }

    .pagination {
        font-size: 10px;
        margin-bottom: 50px;
    }
}

