#view-patient {
    background-color: #BCD9D4;
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-image: url("/public/image1.png"); /* Reemplaza con la ruta de tu imagen */
    background-size: 60%; /* Ajusta la imagen para cubrir todo el contenedor */
    background-position: center; /* Centra la imagen en el contenedor */
    background-repeat: no-repeat; /* Evita que la imagen se repita */
    background-blend-mode: overlay;
}

#header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: small;
    padding-bottom: 20px;
}


#header img {
    height: 50px;
}

#header p {
    font-size: larger;
}

#title {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: small;
    padding-bottom: 20px;
}


#title img {
    height: 50px;
}

#title p {
    font-size: larger;
}

#menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    gap: 1px;
}