/* Button.css */
.custom-menu-button {
    background-color: #E0E0E0;
    color: black;
    opacity: 0.9;
    border: 1px solid gray; 
    padding: 10px; /* Ajusta el padding para controlar el espaciado interno */
    font-size: 18px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    text-align: center;
    display: flex;
    align-items: center; /* Alinea el contenido verticalmente en el centro */
    gap: 10px; /* Espacio entre la imagen y el texto */
    width: 230px; /* Ancho fijo del botón */
    height: 90px; /* Alto fijo del botón */
    overflow: hidden; /* Opcional: oculta cualquier contenido que desborde el botón */
    font-family: 'DM-Sans', sans-serif;

}

.custom-menu-button img {
    height: 45px; /* Ajusta el tamaño de la imagen si es necesario */
    margin-right: 10px; /* Espacio a la derecha de la imagen */
}

.custom-menu-button span {
    flex: 1; /* Permite que el texto ocupe el espacio restante */
    text-align: center; /* Centra el texto horizontalmente */
}

.custom-menu-button:hover {
    background-color: #a8a8a8;
}
